import {RollbarModule} from "@ceros/markup-common/frontend/rollbar-client";
import {generateProxyBundleSrc, generateInjectedScript} from "@ceros/markup-common/shared/transform/dom-manipulation/proxy-bundle-injection";
import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {BlockingPostMessageChannel} from "@ceros/markup-common/frontend/extension/blocking-post-message-channel";
import {EXTENSION_IFRAME_SCRIPT_CHANNEL_NAME} from "@ceros/markup-common/frontend/extension/messages/extension-iframe-script-channel-name";
import {sendMessage} from "../shared/chrome-api-promises";
import {CurrentReleaseHashStorage} from "../shared/storage/current-release-hash-storage";

Logger.init(RollbarModule.EXTENSION_PROJECT_FRAME_CONTENT, process.env.EXTENSION_VERSION!);

((): void => {
    const variablesScript: HTMLScriptElement = document.createElement("script");
    variablesScript.text = `
        ${generateInjectedScript({
            host: window.location.host,
            protocol: window.location.protocol
        })}
        window.MARKUP_EXTENSION_ID = ${JSON.stringify(chrome.runtime.id)};
        window.MARKUP_EXTENSION_VERSION = ${JSON.stringify(process.env.EXTENSION_VERSION)};
    `;

    // inject the variables early before other JS has run
    // at this stage of the tab lifecycle documentElement might be the only element that exists, this also ensures we're first!
    window.document.documentElement.prepend(variablesScript);

    // insert the JS script, ideally this would happen before any other JS has executed, but with extension manifest v2
    // we can't run our code early enough. the JS will load and execute async
    // insert it after the variablesScript to be consistent with proxy
    fireAndForget(
        (async (): Promise<void> => {
            const releaseHash: string = await CurrentReleaseHashStorage.get();
            const script: HTMLScriptElement = document.createElement("script");
            script.src = generateProxyBundleSrc(releaseHash);
            variablesScript.after(script);
        })(),
        Logger
    );

    const channel: BlockingPostMessageChannel = new BlockingPostMessageChannel(
        (message: any): Promise<any> =>
            sendMessage(message).catch((e: Error) => {
                if (e.message === "Extension context invalidated.") {
                    // if the extension gets uninstalled or reloaded, it'll break
                    // this script's ability to communicate with it, so we should stop trying
                    channel.destroy();
                }
            }),
        EXTENSION_IFRAME_SCRIPT_CHANNEL_NAME,
        window,
        window.location.origin
    );

    chrome.runtime.onMessage.addListener((message: any): void => {
        fireAndForget(channel.send(message), Logger);
    });
})();
